import React from 'react';

class Card extends React.Component{
	constructor(){
		super();
		this.state={


		}
	}



	funzioneOnLoad = () => {



		let immagine = document.getElementById(`img${this.props.id}`);


		immagine.classList.toggle("displayNone");
		immagine.classList.add("dib");
		immagine.classList.add("cardDib");

		if(immagine.width>=this.props.prop.inWidth)
			immagine.classList.add("cardFixed");







			this.props.numeri(this.props);



	}

	componentDidUpdate(){
		console.log("updated")
	}


	render(){
		let grow = "grow"
		if(this.props.prop.type==="mobile")
			grow = "growMobile";




			let oKGIF = true;
			const lInk = this.props.link;
			var pos = lInk.lastIndexOf("/");


			const lInkpart2 = lInk.slice(pos, lInk.length);

			const lInkpart3 = lInkpart2.slice((lInkpart2.length-3), lInkpart2.length);
			if(lInkpart3==='gif')
				oKGIF= false;

			let lInkMOD = lInk;
				if(this.props.prop.type!=="mobile"){
					//console.log("desk///////////////")

				}else if(oKGIF){
					//console.log("mobile///////////////")
					const lInkpart1Mobile = lInk.slice(0, pos);
					const lInkpart2Mobile = lInk.slice(pos, lInk.length);
					lInkMOD = lInkpart1Mobile + "/okMobile" + lInkpart2Mobile;

				}









			if(lInkpart3==="jpg"||lInkpart3==="peg"||lInkpart3==="png"||lInkpart3==="gif"){
				return (




					<img

						id={`img${this.props.id}`}
						className={`card br3 ma2 ${grow} shadow-5 displayNone`}
						height={this.props.prop.h}
						width={this.props.prop.w}
						alt={`${this.props.id}`}
						src={`${lInkMOD}`}
						onClick={this.props.click}
						onDoubleClick={this.props.click}
						onError={this.props.erroreCaricamentoImmagini}
						tag={this.props.tag}
						onLoad={this.funzioneOnLoad}
					/>






				);}else{
					return(

							<video

								onMouseEnter={
									(e)=>{

										e.target.controls=true;
									}}
    						onMouseLeave={
									(e)=>{

										e.target.controls=false;
									}
								}
								id={`img${this.props.id}`}
								className={`card br3 ma2 ${grow} shadow-5 displayNone`}
								alt={`${this.props.id}`}
								tag={this.props.tag}
								onClick={this.props.click}
								onDoubleClick={this.props.click}
								onLoadedMetadata={this.funzioneOnLoad}
								preload="metadata"
								height={this.props.prop.h}
								width={this.props.prop.w}
								>
								<source src={this.props.link} type={`video/${lInkpart3}`}></source>
				        <source src="https://versolinfinitoeoltre.altervista.org/errore.mp4" type="video/mp4"></source>

							  Your browser does not support the video tag.
							</video>

					);
				}


	}
}


export default Card;



